.news-banner__title:after { content: ''; display: block; margin-top: 18px; border-top: 1px solid #F92251; }

.sm-btn { display: inline-block; vertical-align: top; padding: 18px 20px 20px; border: 1px solid #fff; width: 100%; max-width: 224px; color: #fff; background: none; font-family: "Roboto Condensed"; font-size: 18px; line-height: 15px; letter-spacing: 0.72px; transition: color 0.25s, background 0.25s; }

.no-touch .sm-btn:hover { color: #000; background: #fff; }

.boxes { position: relative; color: #fff; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.boxes__inner { padding-top: 69px; padding-bottom: 92px; }

@media only screen and (max-width: 767px) { .boxes__inner { padding-top: 50px; padding-bottom: 50px; } }

.box { padding: 32px 50px 42px; border: 1px solid rgba(255, 255, 255, 0.5); text-align: center; }

@media only screen and (max-width: 1260px) { .box { padding: 30px 20px 35px; } }

@media only screen and (max-width: 767px) { .box { padding: 30px 15px 35px; } }

.box__title { font-family: "Roboto Condensed"; font-size: 60px; font-weight: 700; line-height: 79px; letter-spacing: -0.38px; }

@media only screen and (max-width: 1260px) { .box__title { font-size: 48px; line-height: 56px; } }

@media only screen and (max-width: 767px) { .box__title { font-size: 36px; line-height: 46px; } }

.box__title:after { content: ''; display: block; margin: 18px auto 56px; border-radius: 2px; width: 66px; height: 2px; background: #F92251; }

@media only screen and (max-width: 1260px) { .box__title:after { margin: 10px auto 30px; } }

.box__content { margin-bottom: 40px; font-family: "Roboto"; font-size: 16px; font-weight: 300; line-height: 19px; letter-spacing: 0.49px; }

@media only screen and (max-width: 767px) { .box__content { margin: 0 auto 40px; max-width: 420px; } }

.sm-social { max-width: 248px; width: 100%; }

.sm-social__label { margin-bottom: 29px; font-family: "Montserrat"; font-size: 12px; font-weight: 700; line-height: 15px; letter-spacing: 2.4px; text-transform: uppercase; }

.sm-social__items { margin: -20px; font-size: 0.1px; text-align: justify; }

.sm-social__items:after { content: ''; display: inline-block; width: 100%; }

.sm-social__item { display: inline-block; vertical-align: top; padding: 20px; height: 60px; text-align: center; font-size: 0; transition: color 0.25s; }

.sm-social__item:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.no-touch .sm-social__item:hover { color: #F92251; }

.sm-social .icon { display: inline-block; vertical-align: middle; }

.sm-social .icon-facebook { font-size: 19px; }

.sm-social .icon-twitter { font-size: 17px; }

.sm-social .icon-linkedin { font-size: 20px; }

.sm-social .icon-pinterest { font-size: 19px; }

.btn { position: relative; padding: 23px; min-width: 313px; font-family: "Montserrat"; font-size: 15px; font-weight: 400; line-height: 1; vertical-align: top; color: #fff; text-transform: uppercase; border: 1px solid; display: inline-block; background-color: transparent; }

@media only screen and (max-width: 767px) { .btn { min-width: 255px; } }

.btn_red-bord { border-top: 0; border-bottom: 0; border-left: 1px solid #FA537D; border-right: 1px solid #fbd7e1; }

.no-touch .btn_red-bord:hover:before { opacity: 1; }

.btn_red-bord:before { position: absolute; content: ''; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; transition: opacity .3s ease-in-out; background-image: linear-gradient(to left, #fbd7e1 0%, #FA537D 100%); }

.btn_red-bord .btn__text-wrap:before, .btn_red-bord .btn__text-wrap:after { position: absolute; content: ""; left: 0; right: 0; height: 1px; background-image: linear-gradient(to left, #fbd7e1 0%, #FA537D 100%); }

.btn_red-bord .btn__text-wrap:before { top: 0; }

.btn_red-bord .btn__text-wrap:after { bottom: 0; }

.btn_red-bord .btn__text { position: relative; z-index: 1; }

.btn-open-nav { position: relative; z-index: 10; width: 48px; height: 48px; background: none; }

.btn-open-nav.is-active span { width: 19px; border-color: #000; }

.btn-open-nav.is-active span:nth-child(1) { transform: rotate(45deg) translate3d(1px, -3px, 0); }

.btn-open-nav.is-active span:nth-child(2) { opacity: 0; transform: scaleX(0.5); }

.btn-open-nav.is-active span:nth-child(3) { transform: rotate(-45deg) translate3d(1px, 3px, 0); }

.btn-open-nav span { position: absolute; left: 15px; width: 18px; border-top: 2px solid #fff; opacity: 0.87; transition: width 0.3s ease-in-out, opacity 0.3s ease-in-out, transform 0.3s ease-in-out, border-color 0.3s; }

.btn-open-nav span:nth-child(1) { top: 18px; transform-origin: left top; }

.btn-open-nav span:nth-child(2) { top: 23px; transform-origin: left center; }

.btn-open-nav span:nth-child(3) { bottom: 18px; transform-origin: left bottom; }

.footer { color: #fff; background: #000; }

.footer a { color: inherit; }

.footer__top:after, .footer__bottom:after { content: " "; display: table; clear: both; }

.footer__top { padding: 58px 0 75px; }

@media only screen and (max-width: 1260px) { .footer__top { padding: 50px 0; text-align: center; } }

.footer__bottom { padding-top: 29px; padding-bottom: 30px; border-top: 1px solid rgba(255, 255, 255, 0.1); }

.footer__nav, .footer__subnav { font-size: 0; }

.footer__nav li, .footer__nav a, .footer__subnav li, .footer__subnav a { display: inline-block; vertical-align: top; }

@media only screen and (max-width: 767px) { .footer__nav li, .footer__nav a, .footer__subnav li, .footer__subnav a { display: block; } }

.footer__nav a, .footer__subnav a { transition: color 0.25s; }

@media only screen and (max-width: 767px) { .footer__nav a, .footer__subnav a { padding: 6px 0; } }

.no-touch .footer__nav a:hover, .no-touch .footer__subnav a:hover { color: #F92251; }

.footer__nav { float: left; padding: 41px 0 9px; }

@media only screen and (max-width: 1260px) { .footer__nav { float: none; padding: 0 0 40px; } }

.footer__nav li:not(:last-child) { padding-right: 32px; }

@media only screen and (max-width: 767px) { .footer__nav li:not(:last-child) { padding: 0; } }

.footer__nav a { font-family: "HelveticaNeue"; font-size: 12px; line-height: 14px; }

.footer__subnav { float: right; padding: 23px 0 32px; }

@media only screen and (max-width: 767px) { .footer__subnav { float: left; padding: 0; } }

.footer__subnav li:not(:last-child) { padding-right: 98px; }

@media only screen and (max-width: 1260px) { .footer__subnav li:not(:last-child) { padding-right: 60px; } }

@media only screen and (max-width: 767px) { .footer__subnav li:not(:last-child) { padding: 0; } }

.footer__subnav a { font-family: "HelveticaLT"; font-size: 11px; line-height: 12px; letter-spacing: 2.2px; }

.footer__sm-social { float: right; }

@media only screen and (max-width: 1260px) { .footer__sm-social { float: none; margin: 0 auto; } }

.footer__logo { float: left; }

@media only screen and (max-width: 767px) { .footer__logo { float: right; } }

.footer__logo img { display: block; width: 61px; }
